body.darkStyle {
  background: linear-gradient(90deg, rgb(12, 14, 29) 0%, rgb(27, 39, 41) 100%);
  background-size: cover;
  color: white;
}
body.lightStyle {
  background: linear-gradient(
    90deg,
    rgb(214, 219, 255) 0%,
    rgb(190, 178, 199) 100%
  );
  background-size: cover;
}
div.JokeBox {
  display: flex;
  flex-direction: column;
  width: 80%;
  font-size: 1.5rem;
  font-family: sans-serif;
  min-height: 420px;
  margin: 150px 85px;
  border: 2px dotted grey;
  border-radius: 20px;
  color: inherit;
}
div.heading {
  height: 50px;
  margin: 0 auto;
  font-size: 2rem;
  padding-top: 20px;
  font-weight: bolder;
}
div.mainjoke {
  /* min-height: 200px; */
  display: flex;
  justify-content: center;
  height: auto;
  width: 100%;
}

div.mainjoke > div {
  padding: 50px 80px;
  font-size: 1.8rem;
}
.navbar {
  /* display: flex;
  align-items: center;
  justify-content: space-between; */
  width: 70%;
}
button.btn-primary {
  width: 200px;
  height: 50px;
  border-radius: 20px;
  outline: 0;
  border: 0;
  background-color: rgba(25, 255, 255, 0.76);
}
.btnarea {
  display: flex;
  justify-content: center;
}
button.btn-primary:hover {
  transform: scale(1.1);

  transition: 200ms;
}
h3 {
  margin: 0 335px;
  width: auto;
}
.customizeArea .settings img {
  width: 50px;
  aspect-ratio: 1;
}
.customizeArea {
  background: red;
  display: flex;
  justify-content: flex-end;
}
.customizeArea .settings,
.customizeArea .theme {
  display: inline-block;
  cursor: pointer;
}
.customizeArea .settings img::selection {
  background-color: none;
}
div.options {
  /* background-color: green; */
  width: 150px;
  display: flex;
  justify-content: space-around;
}
@media screen and (max-width: 540px) {
  html {
    font-size: 13px;
  }
  div.JokeBox {
    margin: 150px 20px;
  }
}
